import request from './request'

// 
export const userInfo = (id) => {
	return request({
		url: 'Passport/member_info',
		data: {
			m_id: id || ''
		}
	})
}

// 
export const editUserInfo = (option) => {
	return request({
		url: 'Center/modifyInfo',
		mode: 'loading',
		message: '...',
		data: {
			m_id: option.m_id || '',
			avatar: option.avatar || '',
			nickname: option.nickname || ''
		}
	})
}

