// 
export const topupOrderPay = (option) => {
	return request({
		url: 'Center/doBRPay',
		data: {
			m_id: option.m_id || '',
			payment: 2,
			recharge_order_id: option.orderId || ''
		}
	})
}

// 
export const withdrawMoney = (option) => {
	return request({
		url: 'Center/withdraw',
		data: {
			m_id: option.m_id || '',
			amounts: option.amounts || '',
			type: option.type || 1,
			account_name: option.username || '',
			account_number: option.number || ''
		}
	})
}

